.default {
    margin-top: 10px;
    font-weight: 700;
    color: white;
    text-decoration: none;
    padding: .5em 1em calc(.5em + 3px);
    border-radius: 3px;
    background: rgb(64,199,129);
    box-shadow: 0 -3px rgb(53,167,110) inset;
    transition: 0.2s;
}

.default + .default {
    margin-left: 10px;
}
.default:hover { background: rgb(53, 167, 110); }
.default:active {
    background: rgb(33,147,90);
    box-shadow: 0 3px rgb(33,147,90) inset;
}


.red {
    background: red;
}